<template> 
  <van-overlay :show="show" @click="togglePopup">
    <div class="wrapper">
      <div class="panel animate__animated animate__fadeInUpBig" @click.stop>
        
        <mall-item :grid="false" :item="state.detail" :cart="false" :sales="false" class="van-hairline--bottom" :style="{paddingBottom:'10px'}"></mall-item>
        
        <van-cell v-show="hasSpec" v-for="(item,index) in state.detail.spec" :key="index" :title="item.name" :border="false">
          <template #label>
            <van-radio-group v-model="state.options[item.id]" direction="horizontal" checked-color="#ee0a24" @change="choose">
              <van-radio class="radio" v-for="(spec,id) in item.spec" :key="id" :name="id" :class="{active: state.options[item.id] == id}">
                <template #icon></template>{{spec}}
              </van-radio>
            </van-radio-group>
          </template>
        </van-cell>

        <van-cell title="数量" class="number" :border="false">
          <van-stepper v-model="state.num" theme="round" button-size="22" :min="1" :max="state.detail.stock"/>
        </van-cell>

        <van-button type="danger" round block :disabled="state.disabled" @click="save">确 定</van-button>
      
      </div>
    </div>
  </van-overlay>
</template>

<style scoped>
.wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 100%;
}
.panel {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: flex-start;
  padding: 5px 10px;
  width: 100%;
  min-height: 200px;
  background: #FFF;
  border-radius: 10px 10px 0 0;
}

.radio {
  padding-right: 8px;
  border: solid 1px #EEE;
  border-radius: 5px;
}
.radio.active {
  background: #FEE;
  border-color: #ee0a24;
}

.number {
  margin-bottom: 10px;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import MallItem from './MallItem.vue';

export default {
  components: { MallItem },

  props:{
    id: {
      type: [Number, String],
      default: 0,
    },
    show: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    const state = reactive({
      detail: {},

      id: 0,
      num: 1,
      sku: 0,
      spec: "",
      options: {},

      // 确定按钮可用状态
      disabled: true,

      // 底部导航栏显示状态
      tabbarStatus: true,
    });
    return {state};
  },

  computed: {
    hasSpec(){
      return typeof this.state.detail.spec == "object" && !_.isEmpty(this.state.detail.spec) && typeof this.state.detail.sku == "object";
    },
  },

  watch: {
    show(newValue, oldValue){
      if (newValue) {
        this.query();
        this.state.tabbarStatus = this.$store.state.showTabbar;
      }
      this.toggleTabbar(newValue ? false : this.state.tabbarStatus);
    }
  },

  methods: {
    query() {
      this.$http.get("/mall/itemdetail?id=" + this.id).then((data) => {
        this.state.detail = data;
        this.state.disabled = this.hasSpec;
        if (this.id == this.state.id){
          this.hasSpec && this.choose();
        }
        else {
          this.state.num = 1;
          this.state.sku = 0;
          this.state.spec = "";
          this.state.options = {}; 
        }
      });
    },

    choose() {
      let spec = _.toArray(this.state.options).sort().join(",");
      let stock = 0;
      let price = 0;
      for (let i in this.state.detail.sku){
        let obj = this.state.detail.sku[i];
        if (spec == obj.spec.split(",").sort().join(",")){
          this.state.sku = obj.id;
          stock = obj.stock;
          price = obj.price;
          break;
        }
      }

      this.state.disabled = stock == 0;
      this.state.detail.stock = stock;
      this.state.detail.price = price;
    },

    save() {
      const spec = [];
      for (let i in this.state.options){
        for (let j in this.state.detail.spec){
          if (i == this.state.detail.spec[j].id){
            spec.push(this.state.detail.spec[j].name + "：" + this.state.detail.spec[j].spec[this.state.options[i]]);
          }
        }
      }
      this.state.id = this.id;
      this.$emit("save", {
        id: this.state.id,
        num: this.state.num,
        sku: this.state.sku,
        spec: spec,
        digital: this.state.detail.digital,
      });
      this.togglePopup(false);
    },

    ...mapMutations(["togglePopup","toggleTabbar"]),
  },

}
</script>
