<template> 
  <van-row class="item" :class="{grid: isGrid}" :style="{width:width,height:height}" justify="space-between" align="top" @click="forward(item)">
    <van-col class="image" :span="isGrid ? 24 : 8">
      <van-image width="100%" height="100%" radius="5" :fit="isGrid ? 'contain' : 'cover'" :src="item.cover"></van-image>
    </van-col>
    <van-col class="intro" :span="isGrid ? 24 : 16">
      <van-cell :style="{padding: '5px 5px 0 5px'}" :border="false">
        <template #title><div :class="isGrid?'van-ellipsis':'van-multi-ellipsis--l2'">{{item.title}}</div></template>
        <template #label><div class="van-ellipsis" v-show="list >= 100">{{item.feature}}</div></template>
      </van-cell>
      <van-cell :style="{padding: '0 10px 5px 10px'}" :border="false" :center="!isGrid">
        <template #title>
          <span class="price1"><span class="red">￥</span> {{item.price}} </span>
          &nbsp;
          <span class="price2" v-show="!isGrid && item.price_original && item.price_original > 0"> ￥ {{item.price_original}}</span>
        </template>
        <template #label>
          <span class="stock" v-show="!isGrid && stock && item.stock">库存 <span class="red">{{item.stock}}</span> 件 &nbsp;</span>
          <span class="sales" v-show="!isGrid && sales && item.sales">月销 <span class="red">{{item.sales}}</span> 件</span>
        </template>
        <template #right-icon><van-icon v-show="cart" name="cart-o" size="21" color="#F00" @click.stop="$emit('add', item)" /></template>
      </van-cell>
    </van-col>
  </van-row>
</template>

<style scoped>
.item {
  /* width: 100%;
  height: 130px; */
  margin: 8px 5px;
  background: #FFF;
  border-radius: 5px;
  text-align: center;
  overflow: hidden;
}
.image {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.intro {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  text-align: left;
}

.item.grid {
  /* width: 170px;
  height: 270px; */
  display: inline-block;
}
.item.grid .image {
  height: auto;
}
.item.grid .intro {
  height: 100px;
}

.price1 {
  font-size: 14px;
}
.price2 {
  color: #CCC;
  font-size: 12px;
  text-decoration: line-through;
}

.red {
  color: #F00;
}
</style>

<script>
import {mapState, mapGetters, mapMutations} from 'vuex'

export default {
  props:{
    list: {
      type: Number,
      default: 130,
    },
    grid: {
      type: [Boolean, Number],
      default: false,
    },
    stock: {
      type: Boolean,
      default: true,
    },
    sales: {
      type: Boolean,
      default: true,
    },
    cart: {
      type: Boolean,
      default: true,
    },
    item: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  computed: {
    isGrid() {
      if (this.list != 130)
        return false;
      if (typeof this.grid == "boolean")
        return this.grid;
      if (typeof this.grid == "number")
        return true;
      return false;
    },
    width() {
      if (typeof this.grid == "boolean" && !this.grid)
        return "100%";
      return (typeof this.grid == "boolean" ? 170 : this.grid) + "px";
    },
    height() {
      if (typeof this.grid == "boolean" && !this.grid)
        return this.list + "px";
      return (typeof this.grid == "boolean" ? 170 : this.grid) + 100 + "px";
    },
    ...mapState(["channel"]),
  },

  methods: {
    forward(item) {
      let prefix = "/mall/";
      if (/mall\//.test(this.$route.path))
        prefix = "";
      if (typeof item.channel != "undefined" && item.channel != "")
        prefix = "/" + this.channel[item.channel]["type"] + "/";
      let id = typeof item.spu != "undefined" ? item.spu : item.id;
      this.$router.push(prefix + "item?id=" + id);
    }
  },
}
</script>
