<template>
  <BaseLayout title="商品详情" notitle>

    <div v-if="state.detail && state.detail.status">

      <van-swipe class="image" :autoplay="3000" lazy-render>
        <van-swipe-item v-for="image in state.detail.image" :key="image">
          <van-image :src="image" width="100%" fit="contain" />
        </van-swipe-item>
      </van-swipe>

      <div class="group">
        <van-cell center :border="false">
          <template #title><span class="price1"><span class="red">￥</span>{{state.detail.price}}</span></template>
          <template #label>
            <span v-show="state.detail.price_original">市场价&nbsp;&nbsp;<span class="price2">￥{{state.detail.price_original}}</span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span v-show="state.detail.sales">月销量&nbsp;&nbsp;<span class="red">{{state.detail.sales}}</span>&nbsp;件</span>
          </template>
          <template #right-icon><van-icon :name="state.like ? 'like' : 'like-o'" size="28" color="#F00" @click.stop="like(id)" /></template>
        </van-cell>
        <van-cell :title="state.detail.title" class="van-multi-ellipsis--l2 title" :label="state.detail.feature"  :border="false"></van-cell>
      </div>

      <div class="group">
        <van-cell title="商品库存" :value="(state.detail ? state.detail.stock : '0') + ' 件'" ></van-cell>
        <van-cell title="商品型号" :value="state.choose.spec.length == 0 ? '请选择' : state.choose.spec.join('，') " is-link @click="todo()"></van-cell>
        <van-cell title="优惠券" value="无" is-link></van-cell>
      </div>

      <div class="detail" v-html="state.detail.intro"></div>

      <van-action-bar>
        <van-action-bar-icon icon="wap-home-o" text="首页" @click="$router.push('/')" />
        <van-action-bar-icon icon="balance-list-o" text="订单" @click="$router.push('/my/order')" />
        <van-action-bar-icon icon="cart-o" text="购物车" :badge="profile && profile.cartNumber ? profile.cartNumber : ''" @click="$router.push('cart')" />
        <van-action-bar-button v-if="state.detail.digital == 0" type="warning" text="加入购物车" @click="todo('add')" />
        <van-action-bar-button type="danger" text="立即购买" @click="todo('buy')" />
      </van-action-bar>

      <mall-item-chooser :id="id" :show="$store.state.showPopup" @save="save"></mall-item-chooser>

    </div>

    <van-empty v-show="state.detail == null" image="error" description="商品不存在或已下架"></van-empty>

  </BaseLayout>
</template>

<style scoped>
.image {
  width: 100%;
  min-height: 370px;
  padding: 0px;
}
.group {
  margin-top: 8px;
}
.price1 {
  font-size: 21px;
}
.price2 {
  color: #CCC;
  font-size: 12px;
  text-decoration: line-through;
}
.red {
  color: #F00;
}
.title {
  font-size: 18px;
  line-height: 28px;
}

.detail {
  width: 100%;
  margin: 8px 0 50px 0;
  padding: 0;
  min-height: 200px;
  background: #FFF;
  overflow-x: hidden;
}
.detail img {
  width: 100%;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'
import MallItemChooser from '@/components/MallItemChooser.vue'

export default {
  components: {BaseLayout, MallItemChooser},

  props: {
    type: {
      type: String,
      default: "mall",
    },
    id: {
      type: [String, Number],
      default: 1,
    },
  },

  data() {
    const state = reactive({
      // 详情
      detail: {},

      // 喜欢
      like: false,

      // 规格选择
      choose: {
        num: 0,
        spec: [],
      },

      // 动作（加购物车 || 直接购买）
      action: "",

      // 弹出类目
      popup: false,
      category: [],
      active: 0,
      pid: 0,
    });

    return {state};
  },

  created() {
    this.$http.post("/mall/itemdetail?id=" + this.id).then((data) => {
      this.state.detail = _.isEmpty(data) ? null : {...data, ...{
        image: (data.image.length <= 5 ? [data.cover] : [data.cover].concat(data.image.split(","))),
      }};
    });
    
    this.authorized && this.$http.get("/mall/interestList?id=" + this.id).then((data) => {
      this.state.like = !_.isEmpty(data);
    });
  },

  mounted() {
    this.togglePopup(false);
  },

  unmounted() {
    this.togglePopup(false);
  },

  computed: {
    ...mapState(["profile"]),
    ...mapGetters(["authorized"]),
  },

  methods: {
    like(id) {
      this.$http.post("/mall/interestSave?id=" + id + "&remove=" + (this.state.like ? "1" : "0")).then(() => {
        this.state.like = !this.state.like;
      });
    },

    save(obj) {
      this.state.choose = obj;
      this.state.action != "" && this.todo(this.state.action);
    },

    todo(action = ""){
      // console.error(action, this.state.choose);
      this.state.action = action;
      if (action == "" || this.state.choose.num == 0){
        return this.togglePopup(true);
      }
      if (action == "add") {
        this.$http.post("/mall/cartsave?spu=" + this.state.choose.id + "&sku=" + this.state.choose.sku + "&num=" + this.state.choose.num).then((data) => {
          this.cartNumberAdd(this.state.choose.num);
        });
      }
      if (action == "buy") {
        this.$http.post("/mall/cartsave?spu=" + this.state.choose.id + "&sku=" + this.state.choose.sku + "&num=" + this.state.choose.num + "&status=2").then((data) => {
          this.$router.push("bill?direct=1&digital=" + this.state.choose.digital);
        });
      }
    },

    ...mapMutations(["togglePopup","toggleTabbar","cartNumberAdd"]),
  }
}
</script>
